.carouselContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.carouselWrapper {
  display: flex;
  width: 100%;
  position: relative;
}

.carouselContentWrapper {
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.carouselContent {
  display: flex;
  align-items: center;
  transition: all 250ms linear;
  -ms-overflow-style: none;  /* hide scrollbar in IE and Edge */
  scrollbar-width: none;  /* hide scrollbar in Firefox */
  align-items: stretch;
}

/* hide scrollbar in webkit browser */
.carouselContent::-webkit-scrollbar, .carouselContent::-webkit-scrollbar {
  display: none;
}

.carouselContent > * {
  width: 100%;
  flex-shrink: 0;
  flex-grow: 1;
}

.leftArrow, .rightArrow {
  position: absolute;
  z-index: 1;
  top: 50%;
  transform: translateY(-50%);
  width: 48px;
  height: 48px;
  /*border-radius: 24px;*/
  background: #4190ac;
  border: 1px solid #ddd;
  opacity: 0.8;
  border-radius: 4px;
  border: none;
}

.leftArrow {
  left: -20px;
}

.rightArrow {
  right: -20px;
}
